<template>
    <div class="col-md-6 col-md-offset-3">
      <form id="transform"  class="well form-inline" v-on:submit="formSubmit" >
        <input class="form-control" type="text" v-model="textToTranslate" placeholder="请输入要翻译的内容">
        <select class="form-control" v-model="language">
          <option value="en">English</option>
          <option value="ru">Russian</option>
          <option value="ko">Korea</option>
          <option value="ja">Japanese</option>
        </select>
        <input id="zzz" class="btn btn-primary" type="submit" value="翻译">
      </form>
    </div>
</template>

<script>

export default {
  name: 'translateForm',
  data(){
    return{
      textToTranslate:"",
      language:""
    }
  },
  methods:{
    formSubmit(e){
      // console.log(this.textToTranslate);
      this.$emit("formSubmit",this.textToTranslate,this.language);
    }
  },
  created(){
    this.language="en"
  }
}
</script>

<style>
 /*  .col-md-6{
    margin:0 auto;
  } */
  #transform{
    border-radius: 10px;
    border: 1px #ccc solid;
    margin: 0 auto;
  }
</style>



