<template>
  <div id="app">
  <h1>在线翻译器</h1>
  <h5 class="text-muted">简单/易用/便捷</h5>
    <TranslateForm v-on:formSubmit="translateText"></TranslateForm>
    <br><br><br><br> <span>翻译结果是：</span>
    <TranslateOutput v-text="translatedText"></TranslateOutput>
  </div>
</template>

<script>
import TranslateForm from './components/TranslateForm'
import TranslateOutput from './components/TranslateOutput'

import axios from 'axios'

export default {
  name: 'App',
  data(){
    return{
      translatedText:""
    }
  },
  components:{
    TranslateForm,
    TranslateOutput
  },
  methods:{
    translateText(text,language){
      // console.log(text)
      //http 请求翻译
       axios.get('https://translate.yandex.net/api/v1.5/tr.json/translate?key=trnsl.1.1.20180618T075434Z.3e9f92f8648eddfd.638c35f22834c085bf9708b907d4b8c4dc5c35f3&lang='+language+'&text='+text)
        .then( (res) => {
            // console.log(res);
            this.translatedText = res.data.text[0];
        })
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

</style>



